import React from 'react'
import { CSSTransition } from 'react-transition-group'
import './info.css'

const info = 
`2018/10/16
- 效果選項補上「Overdrive特攻」

2018/10/14
- 修正效果選擇「HP」時的Bug
- 排列順序改為由新到舊
- 點選護符後，會開啟wiki分頁
- 部分視覺細節調整

2019/10/8
- 網站上架`


function Info(props) {
  return (
    <CSSTransition classNames='info' in={props.info.active} timeout={250}>
      <div className='info' >
        <div className='info-div'>
          <pre className='info-content'>{info}</pre>
        </div>
      </div>
    </CSSTransition>
  )
}

export default Info