import React from 'react'
import Main from './main'
import Aside from './aside'
import { wyrm_list_5 } from '../data/wyrmprints_5.js'
import { wyrm_list_4 } from '../data/wyrmprints_4.js'
import { wyrm_list_3 } from '../data/wyrmprints_3.js'
import { wyrm_list_2 } from '../data/wyrmprints_2.js'
import { special_regex } from '../data/convert'
import './body.css'

const wyrm_list = [].concat(wyrm_list_5, wyrm_list_4, wyrm_list_3, wyrm_list_2)
const updata_amount = 20
var search_result = []
var main = null

class Body extends React.Component {
  constructor(props) {
    super(props)
    search_result = [].concat(wyrm_list)
    this.state = {
      lists: [],
      hint: true
    }
    this.handleScroll = this.handleScroll.bind(this)
    this.reset = this.reset.bind(this)
  }

  componentDidMount() {
    main = document.getElementsByClassName('main')[0]
    this.checkScrollAndUpdate()
  }

  render() {
    return (
      <div className='body' onScroll={this.handleScroll}>
        <Main value={this.state.lists} hint={this.state.hint}/>
        <Aside handleSubmit={data => this.searchAll(data)} handleReset={this.reset} aside={this.props.aside}/>
      </div>
    );
  }

  handleScroll(e) {
    const reachBottom = (e.target.scrollHeight - e.target.scrollTop - e.target.clientHeight < 1)
    if (reachBottom) {
      if (search_result.length === 0) {
        this.setState({hint: false})
      } else {
        this.addToStateList(updata_amount)
      }
    }
  }

  searchAll(data) {
    let result = this.searchRarity(data.rarity)
    result = this.searchName(data.name, result)
    result = this.searchEffects(data, result)
    search_result = result
    main.scrollTo(0, 0)
    this.setState({lists: [], hint: true})
    this.checkScrollAndUpdate()
  }

  checkScrollAndUpdate() {
    let main = document.getElementsByClassName('main')[0]
    let addList = setInterval(() => {
      if (main.clientHeight === main.scrollHeight) {
        if (search_result.length === 0) {
          clearInterval(addList)
          this.setState({hint: false})
        } else {
          this.addToStateList(updata_amount)
        }
      }
    }, 10)
  }

  addToStateList(amount) {
    let length = search_result.length
    if (length === 0) return

    length = length >= amount ? amount : length
    let temp = search_result.splice(0, length)
    this.setState({lists: this.state.lists.concat(temp)})
  }

  searchRarity(rarity) {
    switch(rarity) {
      case '':
      default:
        return [].concat(wyrm_list)
      case '5':
        return [].concat(wyrm_list_5)
      case '4':
        return [].concat(wyrm_list_4)
      case '3':
        return [].concat(wyrm_list_3)
      case '2':
        return [].concat(wyrm_list_2)
    }
  }

  searchName(name, array) {
    if (name === '') {
      return array
    }
    let temp = []
    let regex = new RegExp('.*' + name + '.*')
    for (let i = 0; i < array.length; i++) {
      if (array[i].name.match(regex)) {
        temp.push(array[i])
      }
    }
    return temp
  }

  searchEffects(data, array) {
    if (data.exclusive === '' && data.condition === '' && data.effect === '') {
      return array 
    }
    let str = ''
    if (data.exclusive !== '')
      str += '【' + data.exclusive + '】.*'
    if (data.condition !== '' && data.condition !== '強擊')
      str += '.*' + data.condition + '.*'
    if (data.effect !== '') {
      if (special_regex[data.effect]) {
        str += '.*' + special_regex[data.effect] + '.*'
      } else {
        str += '.*' + data.effect + '.*'
      }
    }
    if (data.condition === '強擊')
      str += '.*' + data.condition + '.*'
    let regex = new RegExp(str)
    let temp = []
    for (let i = 0; i < array.length; i++) {
      for  (let j = 0; j < array[i].effects.length; j++) {
        if (array[i].effects[j].match(regex)) {
          temp.push(array[i])
          break
        }
      }
    }
    return temp
  }

  reset() {
    this.setState({lists: wyrm_list})
  }
}

export default Body