import React from 'react'
import { CSSTransition } from 'react-transition-group'
import { rarity_list, exclusive_list, condition_list, effect_list } from '../data/select_options'
import './aside.css'

var rarity_option = []
var exclusive_option = []
var condition_option = []
var effect_option = []

function init_option(data, array) {
  array.push(<option key='-' value=''>-</option>)
  for (let item of data) {
    if (item.match(/#.*/)) {
      array.push(<option className='aside-option' key={item} value={item} disabled>{item.replace('#', '')}</option>)
    } else {
      array.push(<option key={item} value={item}>{item}</option>)
    }
  }
}

init_option(rarity_list, rarity_option)
init_option(exclusive_list, exclusive_option)
init_option(condition_list, condition_option)
init_option(effect_list, effect_option)

var inputs = {
  rarity: null,
  effect: null,
  condition: null,
  exclusive: null, 
  name: null
}

function Aside(props) {
  return (
    <div>
      <CSSTransition className='aside' classNames='aside' in={props.aside.active} timeout={500}>
        <div>
          <AsideSelects label='稀有度' input='rarity' list={rarity_option}/>
          <AsideSelects label='效果' input='effect' list={effect_option}/>
          <AsideSelects label='觸發條件' input='condition' list={condition_option}/>
          <AsideSelects label='專屬條件' input='exclusive' list={exclusive_option}/>
          <div className='aside-item'>
            <div className='aside-label'>搜尋名稱</div>
            <input className="aside-input-item" ref={input => inputs.name = input}></input>
          </div>
          <div className='aside-button-div'>
            <button className='aside-button' onClick={() => {
              props.handleSubmit(getInputsData(inputs))
              props.aside.handleClose()
            }}>搜尋</button>
            <button className='aside-button' onClick={() => {
              // props.handleReset()
              clear()
            }} style={{float: 'right'}}>清除</button>
          </div>
        </div>
      </CSSTransition>
    </div>
  )
}

function AsideSelects(props) {
  return(
    <div className='aside-item'>
      <div className='aside-label'>{props.label}</div>
      <select className="aside-input-item" ref={i => inputs[props.input] = i}>
        {props.list}
      </select>
    </div>
  )
}

function getInputsData(inputs) {
  let data = {}
  let keys = Object.keys(inputs)
  let values = Object.values(inputs)
  for (let i = 0; i < keys.length; i++) {
    data[keys[i]] = values[i].value
  }
  return data
}

function clear() {
  inputs.rarity.value = ''
  inputs.effect.value = ''
  inputs.condition.value = ''
  inputs.exclusive.value = ''
  inputs.name.value = ''
}

export default Aside