import React from 'react'
import Header from './components/header'
import Body from './components/body'
import Info from './components/info'
import './App.css'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      aside_active: false,
      info_active: false
    }

    this.closeAside = this.closeAside.bind(this)
    this.reverseAside = this.reverseAside.bind(this)
    this.openInfo = this.openInfo.bind(this)

    document.addEventListener('click', (e) => {
      if (!e.target.className.match(/.*aside.*/)) {
        this.closeAside()
      }
      if (e.target.className.match(/.*info.*/)) {
        this.setState({info_active: false})
      }
    })
  }

  render() {
    return (
      <div className='app'>
        <Header header={{
          handleAsideActiveClick: this.reverseAside,
          handleInfoActiveClick: this.openInfo
        }}/>
        <Body aside={{
          active: this.state.aside_active,
          handleClose: this.closeAside,
          handleReverse: this.reverseAside
        }}/>
        <Info info={{active: this.state.info_active}}/>
      </div>
    )
  }

  closeAside() {this.setState({aside_active: false})}
  reverseAside() {this.setState({aside_active: !this.state.aside_active})}
  openInfo() {this.setState({info_active: true})}
}

export default App