import React from 'react'
import logo from '../images/logo.png'
import './header.css'

function Header(props) {
  return (
    <div className='header'>
      <img className='header-logo' src={logo} alt="logo"></img>
      <div className='header-title'>龍輝護符查詢</div>
      <div className='header-btn' onClick={props.header.handleInfoActiveClick}>？</div>
      <div className='header-btn header-aside-btn' onClick={props.header.handleAsideActiveClick}>≡</div>
    </div>
  )
}

export default Header